import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
  TextField,
} from '@mui/material';
import { Stack } from '@mui/system';
import React, { useCallback, useEffect } from 'react';
import { Controller, useForm } from 'react-hook-form';
import { useRecoilState } from 'recoil';
import { timeTableState } from '../store/store';
import { v4 as uuidv1 } from 'uuid';


const parseTimeToDecimal = (timeStr) => {
  const [h, m] = timeStr.split(":").map(Number);
  return h + m / 60;
};

//      ("HH:mm")
const checkOverLap = (A, B) => {
  const aStart = parseTimeToDecimal(A.startTime);
  const aEnd = parseTimeToDecimal(A.endTime);
  const bStart = parseTimeToDecimal(B.startTime);
  const bEnd = parseTimeToDecimal(B.endTime);

  return !(aEnd <= bStart || aStart >= bEnd);
};

function InputModal({
  showModal,
  handleClose,
  dayData = 'mon',
  startTimeData = '09:00',
  endTimeData = '10:00',
  lectureNameData = '',
  colorData = '#FFFFFF',
  idNum,
}) {
  const {
    formState: { errors },
    control,
    getValues,
    handleSubmit,
    reset,
  } = useForm();

  const [timeTableData, setTimeTableData] = useRecoilState(timeTableState);

  //   
  useEffect(() => {
    if (showModal) {
      reset({
        lectureName: lectureNameData,
        day: dayData,
        startTime: startTimeData,
        endTime: endTimeData,
        lectureColor: colorData,
      });
    }
  }, [showModal, reset, lectureNameData, dayData, startTimeData, endTimeData, colorData]);

  //   
  const Submit = useCallback(
    ({ lectureName, day, startTime, endTime, lectureColor }) => {
      //  
      const valid = !timeTableData[day].some((lec) =>
        checkOverLap(lec, { startTime, endTime })
      );

      if (!valid) {
        alert('    .  .');
        return;
      }

      const data = {
        startTime,
        endTime,
        name: lectureName,
        color: lectureColor,
        id: uuidv1(),
      };

      setTimeTableData((prev) => ({
        ...prev,
        [day]: [...prev[day], data],
      }));

      handleClose();
    },
    [handleClose, setTimeTableData, timeTableData]
  );

  //  
  const Edit = useCallback(
    ({ lectureName, day, startTime, endTime, lectureColor }) => {
      const valid = !timeTableData[day].some(
        (lec) =>
          checkOverLap(lec, { startTime, endTime }) && lec.id !== idNum
      );

      if (!valid) {
        alert('    .  .');
        return;
      }

      const filteredDayData = [
        ...timeTableData[dayData].filter((data) => data.id !== idNum),
      ];

      const newDayData = [
        ...filteredDayData,
        {
          startTime,
          endTime,
          id: idNum,
          name: lectureName,
          color: lectureColor,
        },
      ];

      setTimeTableData({
        ...timeTableData,
        [day]: newDayData,
      });

      handleClose();
    },
    [dayData, handleClose, idNum, setTimeTableData, timeTableData]
  );

  const handleDelete = useCallback(() => {
    setTimeTableData((prev) => ({
      ...prev,
      [dayData]: prev[dayData].filter((lec) => lec.id !== idNum),
    }));
    handleClose();
  }, [dayData, idNum, setTimeTableData, handleClose]);

  return (
    <Dialog open={showModal} onClose={handleClose}>
      <form onSubmit={handleSubmit(idNum ? Edit : Submit)}>
        <DialogTitle align="center">  </DialogTitle>
        <DialogContent style={{ width: '400px' }}>
          {/*  */}
          <Controller
            control={control}
            name="lectureName"
            rules={{ required: true }}
            render={({ field }) => (
              <TextField
                {...field}
                error={!!errors.lectureName}
                style={{ marginTop: '30px', width: '350px' }}
                autoComplete="off"
                label=""
              />
            )}
          />

          {/*  */}
          <FormControl style={{ marginTop: '30px' }}>
            <FormLabel></FormLabel>
            <Controller
              control={control}
              name="day"
              rules={{ required: true }}
              render={({ field }) => (
                <RadioGroup {...field} style={{ display: 'block' }}>
                  <FormControlLabel value="mon" control={<Radio />} label="" />
                  <FormControlLabel value="tue" control={<Radio />} label="" />
                  <FormControlLabel value="wed" control={<Radio />} label="" />
                  <FormControlLabel value="thu" control={<Radio />} label="" />
                  <FormControlLabel value="fri" control={<Radio />} label="" />
                </RadioGroup>
              )}
            />
          </FormControl>

          {/* /  */}
          <Stack spacing={3} style={{ marginTop: '30px', width: '350px' }}>
            <Controller
              control={control}
              name="startTime"
              rules={{ required: true }}
              render={({ field }) => (
                <TextField
                  {...field}
                  type="time"
                  error={!!errors.startTime}
                  style={{ marginTop: '10px', width: '350px' }}
                  label=" "
                  InputLabelProps={{ shrink: true }}
                />
              )}
            />
            <Controller
              control={control}
              name="endTime"
              rules={{
                required: true,
                validate: (value) => value > getValues('startTime'),
              }}
              render={({ field }) => (
                <TextField
                  {...field}
                  type="time"
                  error={!!errors.endTime}
                  style={{ marginTop: '10px', width: '350px' }}
                  label=" "
                  InputLabelProps={{ shrink: true }}
                />
              )}
            />
          </Stack>

          {/*  */}
          <div style={{ marginTop: '30px' }}>
            <label htmlFor="lectureColor"> :</label>
            <Controller
              control={control}
              name="lectureColor"
              render={({ field }) => (
                <input
                  {...field}
                  style={{ marginLeft: '30px' }}
                  id="lectureColor"
                  type="color"
                />
              )}
            />
          </div>
        </DialogContent>
        <DialogActions>
          {idNum && (
            <Button color="error" onClick={() => handleDelete()}>
              
            </Button>
          )}
          <Button onClick={handleClose}></Button>
          <Button type="submit"></Button>
        </DialogActions>
      </form>
    </Dialog>
  );
}

export default InputModal;
